import sun from '../images/sun.svg';
import rain from '../images/rain.svg';
import cloudy from '../images/cloudy.svg';
import mainlyCloudy from '../images/mainly_cloudy.svg'
import smallRain from '../images/small_rain.svg';
import smallRainSun from '../images/small_rain_sun.svg';
import show from '../images/snow.svg';

const iconsWeather = {
    mainlyCloudy: mainlyCloudy,
    cloudy:cloudy,
    rain:rain,
    sun:sun,
    show:show,
    smallRain:smallRain,
    smallRainSun:smallRainSun
}
export let currentIcon;
export function foreachIconsWeather(value){
    
    switch(value){
        case "": currentIcon=iconsWeather.sun;
        break;
        case "": currentIcon=iconsWeather.cloudy;
        break;
        case " ": currentIcon=iconsWeather.mainlyCloudy;
        break;
        case " ": currentIcon=iconsWeather.mainlyCloudy;
        break;
        case "  ": currentIcon=iconsWeather.mainlyCloudy;
        break;
        case " ": currentIcon=iconsWeather.smallRain;
        break;
        case " ": currentIcon=iconsWeather.smallRainSun;
        break;
        case "": currentIcon=iconsWeather.rain;
        break;
        case "": currentIcon=iconsWeather.rain;
        break;
        case "": currentIcon=iconsWeather.rain;
        break;
        case "": currentIcon=iconsWeather.show;
        break;
        case " ": currentIcon=iconsWeather.show;
        break;
        default: ;
    }
    return currentIcon;
}